*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Regular.4cad464e.woff") format("woff"), url("Roboto-Regular.63f26da3.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Poppins;
  src: url("Poppins-Regular.6c5d0fe0.woff") format("woff"), url("Poppins-Regular.dbaa922b.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Manrope;
  src: url("Manrope-Regular.9604a740.woff") format("woff"), url("Manrope-Regular.b4e76829.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Manrope;
  src: url("Manrope-Medium.4e562fd1.woff") format("woff"), url("Manrope-Medium.6efe16f5.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Manrope;
  src: url("Manrope-Bold.9c040a17.woff") format("woff"), url("Manrope-Bold.46a1ae1d.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

h1, h2, h3, h4, h5, h6, p, ul, table, th, td {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.mobile-menu__nav-link, .mobile-menu__logo {
  color: currentColor;
  text-decoration: none;
}

:root {
  --white-main-color: #fff;
  --black-main-color: #111321;
  --body-color-light-theme: #f4f4f4;
  --body-color-dark-theme: #686868;
  --grey-main-color: #a8a8a8;
  --blue-main-color: #4440f6;
  --blue-color: #4440f7;
  --blue-secondary-color: #4b48db;
  --transparent-blue-color: rgba(68, 64, 247, .7);
  --transparent-secondary-blue-color: rgba(68, 64, 247, .4);
  --dark-color: #2e2e2e;
  --green-color: #00dd73;
  --blured-card-backdrop-color: rgba(244, 244, 244, .4);
  --transparent-backdrop-location-weather-card: rgba(254, 254, 255, .3);
  --dark-grey-text-color: #5f6775;
  --light-grey-text-color: #a8a8a8;
  --main-black-text-color: #111321;
  --white-text-color: #fff;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header {
  padding: 24px 0;
}

.container {
  width: 320px;
  margin: 0 auto;
  padding: 0 16px;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__link {
  color: var(--black-main-color);
  font-family: Manrope;
  font-size: 42px;
  font-style: normal;
  font-weight: 700;
  line-height: 57px;
  text-decoration: none;
}

.dark-theme .header__link {
  color: var(--body-color-light-theme);
  padding-top: 55px;
  padding-bottom: 33px;
  display: block;
}

.nav__list {
  margin-left: 102px;
  margin-right: 147px;
  display: flex;
}

.nav__item {
  margin-right: 69px;
  list-style: none;
}

.nav__link {
  color: var(--black-main-color);
  font-family: Manrope;
  font-size: 18px;
  font-weight: 700;
  line-height: 25px;
  text-decoration: none;
}

.form-search {
  position: relative;
}

.form-search__input {
  width: 173px;
  height: 31px;
  border: none;
  border: 1px solid var(--black-main-color);
  color: var(--black-main-color);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 20px;
  padding: 5px 5px 5px 48px;
  transition: transform .32s linear;
  transform: scale(0);
}

.dark-theme .form-search__input {
  color: var(--body-color-light-theme);
  border: 1px solid var(--body-color-light-theme);
}

.screen-tablet .form-search__input, .screen-desktop .form-search__input {
  transform: scale(1);
}

.form-search__input::-ms-input-placeholder {
  letter-spacing: .02em;
  opacity: .4;
  color: var(--black-main-color);
  font-family: Poppins;
  font-size: 12px;
  line-height: 21px;
}

.form-search__input::placeholder {
  letter-spacing: .02em;
  opacity: .4;
  color: var(--black-main-color);
  font-family: Poppins;
  font-size: 12px;
  line-height: 21px;
}

.dark-theme .form-search__input::-ms-input-placeholder {
  color: var(--body-color-light-theme);
}

.dark-theme .form-search__input::placeholder {
  color: var(--body-color-light-theme);
}

.form-search__button {
  width: 20px;
  height: 20px;
  fill: var(--black-main-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  position: absolute;
  top: 8px;
  right: 14px;
}

.dark-theme .form-search__button {
  fill: var(--body-color-light-theme);
}

.header .switch__wrapper {
  display: none;
}

.screen-tablet .header .switch__wrapper, .header .switch__wrapper screen-desktop {
  display: block;
}

.switch__wrapper {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch__icon {
  fill: currentColor;
  transition: fill .32s linear;
}

@media screen and (min-width: 768px) {
  .switch__icon {
    display: none;
  }
}

.switch__text {
  color: inherit;
  transition: color .32s linear;
  display: none;
}

@media screen and (min-width: 768px) {
  .switch__text {
    display: block;
  }
}

.light-theme .light-theme-box, .light-theme .dark-theme-box {
  color: #5f6775;
}

.dark-theme .dark-theme-box, .dark-theme .light-theme-box {
  color: #f4f4f4;
}

.dark-theme-box.active, .light-theme-box.active {
  color: #4440f7;
}

.light-theme-box, .dark-theme-box {
  display: flex;
}

.slider {
  cursor: pointer;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background: #4b48db center no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.switch__input:checked + .slider {
  background-color: #4440f7;
}

.switch__input:checked + .slider:before {
  background: #fff center no-repeat;
  transform: translateX(22px);
}

.slider.round {
  border: 1px solid #4b48db;
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

body.dark-theme {
  background-color: #2e2e2e;
}

body.light-theme {
  background-color: #f4f4f4;
}

.mobile-menu {
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: var(--body-color-light-theme);
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-100%);
}

.mobile-menu--dark {
  background-color: var(--body-color-dark-theme);
}

.mobile-menu.is-open {
  transform: translateX(0);
}

.mobile-menu__logo {
  color: var(--main-black-text-color);
  margin-bottom: 44px;
  padding: 24px 0 24px 16px;
  font-family: Manrope;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.375;
}

.mobile-menu__close-button {
  cursor: pointer;
  color: var(--main-black-text-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 28px;
  right: 16px;
}

@media screen and (min-width: 768px) {
  .mobile-menu__close-button {
    display: none;
  }
}

.mobile-menu__close-button:hover, .mobile-menu__close-button:focus {
  color: var(--blue-main-color);
}

.mobile-menu__icon {
  fill: var(--body-color-light-theme);
  stroke: var(--blue-main-color);
}

.mobile-menu__icon--close {
  stroke: currentColor;
}

.mobile-menu__box-icon {
  width: 31px;
  height: 31px;
  border: 1px solid var(--body-color-light-theme);
  background-color: var(--blue-main-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.mobile-menu__box-icon--right {
  border: none;
  margin-left: auto;
}

.mobile-menu__container {
  height: 100%;
  flex-direction: column;
  display: flex;
}

.mobile-menu__nav {
  flex-direction: column;
  gap: 24px;
  margin-bottom: auto;
  display: flex;
  position: relative;
}

.mobile-menu__nav-link {
  color: var(--main-black-text-color);
  align-items: center;
  gap: 14px;
  padding-left: 16px;
  font-family: Manrope;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.375;
  display: flex;
}

.mobile-menu__current-page {
  background-color: var(--blue-main-color);
  color: var(--body-color-light-theme);
  padding: 6px 10px 6px 16px;
}

.categories__other-box {
  height: 380px;
