// Ð¼ÑÐºÑÐ¸Ð½ Ð´Ð»Ñ ÑÑÐ¸Ð»ÑÐ·Ð°ÑÑÑ ÑÐµÐºÑÑÑ
// Ð´Ð»Ñ Ð´Ð¾Ð´Ð°Ð²Ð°Ð½Ð½Ñ Ð² ÑÑÐ¸Ð»Ñ: @include text(700, 32px, 44px, -0.01em);
@mixin text($font-weight, $font-size, $line-height, $letter-spacing) {
  font-weight: $font-weight;
  font-size: $font-size;
  line-height: calc($line-height/$font-size);
  letter-spacing: $letter-spacing;
}
//  Ð¼ÑÐºÑÐ¸Ð½Ð¸ Ð´Ð»Ñ Ð¼ÐµÐ´ÑÐ° Ð·Ð°Ð¿Ð¸ÑÑÐ²
//  Ð´Ð»Ñ Ð²Ð¸ÐºÐ¾ÑÐ¸ÑÑÐ°Ð½Ð½Ñ Ð² ÐºÐ¾Ð´Ñ
// @include for-size(desktop) {
//   background-position-y: 35%;
// }
@mixin for-size($range) {
  $mobile: 320px;
  $tablet: 768px;
  $desktop: 1280px;

  @if $range == mobile-only {
    @media (max-width: #{$tablet - 1}) {
      @content;
    }
  } @else if $range == mobile {
    @media (min-width: $mobile) {
      @content;
    }
  } @else if $range == tablet {
    @media (min-width: $tablet) {
      @content;
    }
  } @else if $range == tablet-only {
    @media (max-width: #{$desktop - 1}) {
      @content;
    }
  } @else if $range == desktop-only {
    @media (max-width: #{$desktop - 1}) {
      @content;
    }
  } @else if $range == desktop {
    @media (min-width: $desktop) {
      @content;
    }
  }
}
// Ð¼ÑÐºÑÐ¸Ð½ Ð´Ð»Ñ ÑÑÑÐµÑÐ°
@mixin footer-size-tablet($height) {
  @media (min-width: 768px) {
    height: $height;
    @content;
  }
}

// Ð´Ð»Ñ Ð´Ð¾Ð´Ð°Ð²Ð°Ð½Ð½Ñ Ð² ÑÑÐ¸Ð»Ñ: font-family: font(_Ð½Ð°Ð·Ð²Ð° ÑÑÐ¸ÑÑÑ,ÑÐºÐ¸Ð¹ Ð¿Ð¾ÑÑÑÐ±Ð½Ð¾);
$font: (
  _manrope: (
    'Manrope',
  ),
  _roboto: (
    'Roboto',
  ),
  _poppins: (
    'Poppins',
    sans-serif,
